import {
    Scene, AnimationMixer, MeshPhysicalMaterial, SpotLight, Mesh, MeshLambertMaterial,
    WebGLRenderer, PerspectiveCamera, PlaneGeometry, Vector2, Clock, MathUtils, PCFSoftShadowMap, ACESFilmicToneMapping
} from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js'
import { clone } from 'three/examples/jsm/utils/SkeletonUtils.js'
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer.js'
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass.js'
import { UnrealBloomPass } from 'three/examples/jsm/postprocessing/UnrealBloomPass.js'
import { OutputPass } from 'three/examples/jsm/postprocessing/OutputPass.js'

let fightA, mixerFighterA, fightB, mixerFighterB, animations, composer

const scene = new Scene()
const gltfLoader = new GLTFLoader()

gltfLoader.load(
    '/models/zfighter.gltf',
    (gltf) => {
        // add fighterA
        let fighterA = gltf.scene
        fighterA.frustumCulled = false
        animations = gltf.animations
        let v = 1
        fighterA.scale.set(v, v, v)
        fighterA.traverse((node) => {
            if (!node.isMesh) return
            let clonedMaterial = node.material.clone()
            clonedMaterial.metalness = 0.3
            clonedMaterial.roughness = 0.2
            node.material = clonedMaterial
            node.castShadow = true
        })
        fighterA.castShadow = true
        scene.add(fighterA)

        // create animation mixer for fighter A
        mixerFighterA = new AnimationMixer(fighterA)
        fightA = mixerFighterA.clipAction(animations[0])
        fightA.play()

        // add fighter B
        let fighterB = clone(fighterA)
        fighterB.traverse((node) => {
            if (!node.isMesh) return
            let clonedMaterial = node.material.clone()
            let mat = new MeshPhysicalMaterial({ color: 0x00ff00 })
            mat.normalMap = clonedMaterial.normalMap
            mat.specularIntensityMap = clonedMaterial.specularIntensityMap
            mat.metalness = 0.3
            mat.roughness = 0.2
            node.material = mat
            node.castShadow = true
        })
        fighterB.rotateZ(0.000001)
        fighterB.rotateY(0.000001)
        fighterB.castShadow = true
        scene.add(fighterB)

        // create animation mixer for fighter B
        mixerFighterB = new AnimationMixer(fighterB)
        fightB = mixerFighterB.clipAction(animations[0])
        fightB.play()
    },
    (_) => { },
    (error) => {
        console.log(error)
    }
)

// Add spotlight
const spotLight = new SpotLight(0xffffff, 270)
spotLight.position.set(2, 4, 5)
spotLight.angle = 0.52
spotLight.penumbra = 1
spotLight.focus = 1
spotLight.decay = 2
spotLight.castShadow = true
spotLight.shadow.camera.near = 0.1
spotLight.shadow.camera.far = 18
spotLight.shadow.bias = - 0.000222
spotLight.shadow.mapSize.width = 2048
spotLight.shadow.mapSize.height = 2048
scene.add(spotLight)

// Plane
const planeGeometry = new PlaneGeometry(200, 200)
planeGeometry.rotateX(- Math.PI / 2)
const planeMaterial = new MeshLambertMaterial({ color: 0xbcbcbc })
const plane = new Mesh(planeGeometry, planeMaterial)
plane.receiveShadow = true
scene.add(plane)

// Camera
const camera = new PerspectiveCamera(
    75,
    window.innerWidth / window.innerHeight,
    0.01,
    1000
)
camera.position.set(0, 1.5, 5) // Adjust camera position to view the model
scene.add(camera)

const canvas = document.querySelector('canvas.animation')

// Limitate controls
const controls = new OrbitControls(camera, canvas)
controls.enableDamping = true
controls.target.set(0, 0.8, 0)
controls.maxPolarAngle = MathUtils.degToRad(90)
controls.maxDistance = 4
controls.minDistance = 1
controls.enablePan = false

const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

// Renderer
const renderer = new WebGLRenderer({ canvas: canvas, antialias: true })
renderer.setPixelRatio(window.devicePixelRatio)
renderer.setSize(sizes.width, sizes.height)
renderer.shadowMap.enabled = true
renderer.shadowMap.type = PCFSoftShadowMap
renderer.toneMapping = ACESFilmicToneMapping
renderer.toneMappingExposure = 0.855

// Bloom
const renderScene = new RenderPass(scene, camera)

const bloomPass = new UnrealBloomPass(new Vector2(window.innerWidth, window.innerHeight), 1.5, 0.4, 0.85)
bloomPass.threshold = 0
bloomPass.strength = 0.18
bloomPass.radius = 0.5

const outputPass = new OutputPass()

composer = new EffectComposer(renderer)
composer.addPass(renderScene)
composer.addPass(bloomPass)
composer.addPass(outputPass)

const clock = new Clock()

const tick = () => {
    let d = clock.getDelta()
    if (mixerFighterA && mixerFighterB) {
        mixerFighterA.update(d)
        mixerFighterB.update(d)
    }
    controls.update()

    composer.render()
    window.requestAnimationFrame(tick)
}

tick()

window.addEventListener('resize', () => {
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
    composer.setSize(sizes.width, sizes.height)
})
